/* PieContainer */

.pieContainer {
  padding: 30px;
  height: 1200px;
  position: relative;
}

.pieBackground {
  position: absolute;
  width: 1200px;
  height: 1200px;
  border-radius: 100%;
  box-shadow: 0 0 32px rgba(0,0,0,0.5);
}

.pie {
  position: absolute;
  width: 1200px;
  height: 1200px;
  border-radius: 100%;
  clip: rect(0px, 600px, 1200px, 0px);
}

.hold {
  position: absolute;
  width: 1200px;
  height: 1200px;
  border-radius: 100%;
  clip: rect(0px, 1200px, 1200px, 600px);
}

.recharts-legend-item {
  line-height: 1.2;
  font-size: 0.8em;
  margin-right: 30px;
}

